type Options = {
  removeEmptyString?: boolean
}

export function removeEmptyKeys<T extends Record<string, unknown>>(obj: T, options?: Options): T {
  const result = { ...obj }
  Object.keys(result).forEach((key) => {
    if (obj[key] === undefined || obj[key] === null) {
      delete result[key]
    }

    if (options?.removeEmptyString && obj[key] === '') {
      delete result[key]
    }
  })

  return result
}
