function isNumber<T>(target: T) {
  try {
    return !isNaN(+target)
  } catch (e) {
    return false
  }
}

export default function validVersion<T>(target: T): boolean {
  if (!target) return false
  try {
    const splitVersions = target
      .toString()
      .split('.')
      .map((item) => item.trim())
      .filter(Boolean)
    if (splitVersions.length !== 3) return false
    const [major, minor, patch] = splitVersions
    return isNumber(major) && isNumber(minor) && isNumber(patch)
  } catch (e) {
    return false
  }
}
